* {
  color: #828ea0;
  font-family: Gotham Pro;
}
.mainContainer{
    width: 98%;
    margin: auto;
}
.main {
  height: 10vh;

  display: grid;
  grid-template-columns: 1fr 1fr 2fr 0.5fr 1fr 0.5fr;
  padding: 3vh;
}
.logotype {
  width: 7vw;
  
}
.user,
.chartType,
.interval,
.accountType {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.user > svg {
  width: 2vh;
  height: 2vh;
  margin-right: 1vw;
}
.user > svg > g > path {
  fill: #828ea0;
}
.chartType {
}
.interval {
}
.button {
  width: 100%;
  height: 5vh;

  display: flex;
  justify-content: center;
  align-items: center;

  border-radius: 10px;

  background-color: #fff700;
  color: initial;

  font-size: 14pt;
  font-weight: bold;
}

.currencyContainer {
  display: grid;
  grid-template-columns: 0.5fr repeat(10, 1fr);
  padding-left: 1vw;
}
.currencyPlus{
    display: flex;
    justify-content: center;
    align-items: center;
}
.currencyPlus>svg{
    height: 30px;
}
.currencyPlus>svg>path{
    fill: #828ea0;
}

.mainBigBlock{
 width: 100%;
 display: grid;
 grid-template-columns: 8fr 2fr;   
}
.mainBigBlock>*{
    border: 1px solid red;
}

.mainChartWithPrice{
    display: grid;
    grid-template-rows: 9fr 1fr;

    height: 85vh;
}

.chartBlock{
    margin-top: 2vh;

    width: 80vw;
    background-color: #111;

    display: flex;
    justify-content: center;
    align-items: center;
}

.price{
    width: 100%;
}

.history{
    display: flex;
    justify-content: flex-start;
    align-items: center;

    height: 50%;
    padding-left: 20px;

    font-size: 16pt;
}